<template>
    <el-dialog :title="formTitle" :value="value" v-model="visible" @close="resetFormData">
        <el-form :model="formData" label-width="120px" :rules="formRules" ref="formData">
            <el-form-item label="商家名称" prop="name" required>
                <el-input v-model.trim="formData.name" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item  label="商家类型" prop="type" required>
                <template>
                    <el-select style="width: 50%;" v-model="formData.type" placeholder="请选择商家类型" disabled v-if="formData.id!=''">
                        <el-option v-for="item in authorTypes" :key="item.id" :label="item.name" :value="item.id"></el-option>
                    </el-select>
                    <el-select style="width: 50%;" v-model="formData.type" placeholder="请选择商家类型" v-else>
                        <el-option v-for="item in authorTypes" :key="item.id" :label="item.name" :value="item.id"></el-option>
                    </el-select>
                </template>
            </el-form-item>
            <el-form-item  label="联系手机" prop="phone" required>
                <el-input v-model="formData.phone" auto-complete="off"></el-input>
            </el-form-item>

            <el-form-item  label="商家简介" prop="description" required>
                <el-input
                    type="textarea"
                    :autosize="{ minRows: 1, maxRows: 10}"
                    placeholder="请输入商家简介"
                    v-model="formData.description">
                </el-input>
            </el-form-item>

            <el-form-item label="商家头像" prop="coverId" required>
                <el-upload class="avatar-uploader"
                           action="/ks3/image_upload"
                           :show-file-list="false"
                           :on-success="onSuccess">
                    <img v-if="formData.coverUrl" :src="formData.coverUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="visible = false">取消</el-button>
            <el-button size="small" type="primary" @click.native="formSubmit" :loading="formLoading">提交</el-button>
        </div>
    </el-dialog>
</template>

<script type="es6">
    import util from '../../../api/util'
    import {httpPost} from '../../../api/api';

    export default {
        name: 'vDetail',
        props: ['value', 'data', 'authorTypeList'],
        data() {
            let check = (rule, value, callback) => {
                let reg = /^1[34578]\d{9}$/;
                if (!reg.test(value)) {
                    callback(new Error('联系手机格式有误'));
                }
                callback();
            };

            return {
                visible: false, //默认隐藏
                showLoading: false,
                formTitle: '新增商家',
                formLoading: false,
                formRules: {
                    name: [
                        {required: true, message: '请输入商家名称', trigger: 'blur'},
                        {min: 1, max: 32, message: '长度在 1 到 32 个字符', trigger: 'blur'}
                    ],
                    description: [
                        {required: true, message: '请输入商家简介', trigger: 'blur'},
                    ],
                    phone:[
                        {required: true, message: '请输入联系电话', trigger: 'blur'},
                        {validator: check, trigger: 'blur'}
                    ]
                },
                formData: {
                    id: '',
                    name: '',
                    type: '',
                    phone: '',
                    description: '',
                    coverId: '',
                    coverUrl: ''
                },
                inputVisible: false, //隐藏、显示关键字输入框
                inputValue: '',
                avatarLoading: false,
                authorTypes: []
            }
        },
        computed: {
            detail() { //返回详情
                let _self = this;
                this.authorTypes = this.authorTypeList;
                if (!_self.value) { // 弹窗不显示，不进行请求
                    return;
                }
                if (_self.data.id && _self.visible) {
                    _self.formTitle = '编辑商家';
                    _self.formData = _self.data;
                }
            }
        },
        methods: {
            formSubmit() { //提交表单
                let _self = this;

                let coverId = _self.formData.coverId;
                if (!coverId) {
                    _self.$message.error('请上传商家头像');
                }

                let para = new Object();
                para.id=_self.formData.id;
                para.name=_self.formData.name;
                para.type= _self.formData.type;
                para.phone=_self.formData.phone;
                para.description=_self.formData.description;
                para.coverId=coverId;

                _self.$refs.formData.validate((valid) => {
                    if (valid) {
                        _self.formLoading = true;
                        httpPost('authorSave', para, _self, function (res) {
                            _self.formLoading = false;
                            try {
                                _self.$message.success('提交成功');
                                _self.$refs['formData'].resetFields();
                                _self.visible = false;
                                _self.$emit('refresh');
                            } catch (error) {
                                util.jsErrNotify(error);
                            }
                        });
                    }
                });
            },
            onSuccess(res, file) {
                this.formData.coverUrl = URL.createObjectURL(file.raw);
                this.formData.coverId = res.data.id;
            },
            resetFormData() { //关闭表格弹窗，重置表格数据
                let _self = this;
                _self.formTitle = '新增商家';
                _self.formData = {
                    id: '',
                    name: '',
                    type: '',
                    phone: '',
                    description: '',
                    coverId: '',
                    coverUrl: ''
                };
                _self.data = {};
            }
        },
        watch: {
            detail(val) { //监测详情变化
            },
            value(val) {
                this.visible = val;
            },
            visible(val) {
                this.$emit('input', val);
            }
        },
        mounted() {
            if (this.value) {
                this.visible = true;
            }
        }
    }
</script>



